@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);

* {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    -webkit-margin-before: 0rem;
            margin-block-start: 0rem;
    -webkit-margin-after: 0rem;
            margin-block-end: 0rem;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
.messages-screen > *{
    margin: 0;
}
.navbar {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    justify-content: space-between;
    background-color: white;
}
/* .nav-box-1 ul, .nav-box-2 h1, .nav-box-3 ul, .messages-list{
    margin-block-start: 0rem;
    margin-block-end: 0rem;
    padding-inline-start: 0;
} */

.add-friends {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(221, 221, 221);
    width:100%;
    position: absolute;
    min-height: 100vh;
}
.add-friends-content {
    justify-content: space-between !important;
}
.search-bar {
    width: 100%;
    display: flex;
    justify-content: center;
}
.quick-add {
    width:96%;
}
.add-navbar {
    /* position: absolute; */

    top: 0;
    width:100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    justify-content: space-between;
}
.add-title-navbar {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    justify-content: center;
}
.add-body {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.add-box-1, .add-box-2 {
    display: flex;
    align-items: center;
    flex-direction: row;
}
.add-box-2 {
    justify-content: flex-end;
}
.add-footer {
    margin: 2rem 0 2rem 0;
}
.added, .removed{
    padding: 0.2rem 1rem 0.2rem 1rem;
    border: none;
    border-radius: 1rem;
    margin: 0 0.5rem 0 0.5rem;
    font-size: 13.3px;
}
.added {
    background-color: rgb(155, 235, 155);
}
.removed {
    background-color: rgb(248, 178, 178);
}

button {
    padding: 0.2rem 1rem 0.2rem 1rem;
    border: none;
    border-radius: 1rem;
    background-color: rgb(216, 216, 216);
    margin: 0 0.5rem 0 0.5rem;
}
button:hover {
    background-color: rgb(233, 233, 233);
}
.list-container {
    width: 96% !important;
    border-radius: 1rem;
    border: none;
    background-color: white;
}
input[type="search"], input[type="search"]:focus {
    margin: 0.5rem;
    padding: 1rem 1.5rem 1rem 1.5rem;
    border-radius: 10rem;
    border: none;
    outline: none;
    width: 98%;
    background-color: lightgrey;
    font-size: 1rem;
}

div[class*="nav-box"] ul {
    display:flex;
    flex-direction: row;
    align-items: center;
}
div[class*="nav-box"] ul li {
    margin: 0 0.7rem 0 0.7rem;
}
ul[class*="messages-list"] li, ul[class*="add-list"] li {
    display: flex;
    justify-content: flex-start;
    padding: 1rem 1rem 1rem 1rem;
}
ul[class*="add-list"] li:not(:last-child) {
    border-bottom: 1px rgb(170, 170, 170) solid;
}
ul[class*="messages-list"] li, ul[class*="add-list"] {
    border-bottom: 1px rgb(170, 170, 170) solid;
}
li {
    text-decoration: none;
    list-style-type: none;
}
a {
    padding: 0.3rem 0.3rem 0.3rem 0.3rem;
    border-radius: 0.25rem;
}
a:hover {
    background-color: lightblue;
    cursor: pointer;
}
.welcome-statement {
    background-color:rgb(216, 216, 216);
}
.message-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left:1rem;
}
.message-sub-info {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.message-sub-info > * {
    margin-right: 0.5rem;
}
.message-content {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.message-avatar {
    height: 2.5rem;
    width:2.5rem;
    border-radius: 100%;
}
.message-new {
    height: 0.95rem;
    width: 0.95rem;
    background-color: red;
    border-radius: 0.2rem;
}
.message-received {
    height: 0.6rem;
    width: 0.6rem;
    background-color: none;
    border: 0.17rem solid red;
    border-radius: 0.2rem;
}
.message-opened {
    width: 0;
	height: 0;
	border-top: 0.5rem solid transparent;
	border-left: 1rem solid #555;
	border-bottom: 0.5rem solid transparent;
}
.message-sent {
    width: 0;
	height: 0;
	border-top: 0.5rem solid transparent;
	border-left: 1rem solid #664db1;
	border-bottom: 0.5rem solid transparent;
}
.red {
    color: red;
}
.profile-pic {
    height: 2rem;
    width: 2rem;
    border-radius: 100%;
}
.profile-pic:hover {
    cursor: pointer;
    opacity: 70%;
}
.image-background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
}
.image-desktop {
    width: 100% !important;
    overflow-y: hidden;
    overflow-x: hidden;
}
.image-mobile {
    height : 100% !important;
    width: auto;
    overflow-y: hidden;
    overflow-x: hidden;
}
.selected {
    background-color: rgb(31, 168, 247);
    height: 80%;
    width: 100%;
    border-radius: 100%;
}
.selected:hover {
    background-color: rgb(114, 192, 218);
}
.unselected {
    height: 80%;
    width: 100%;
    border-radius: 100%;
}
.webcam-screen {
    height:100vh;
    overflow-y: hidden;
    overflow-x: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    flex-direction: column;
}
/* .webcam-desktop {
    width: 100%;
}
.webcam-mobile {
    height:100%;
} */
.webcam-overlay, .captured-overlay {
    z-index: 10;
    height: 100%;
    width:100%;
    position: absolute;
    top:0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.captured-overlay {
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
}
.send_to-overlay {
    z-index: 10;
    height: 100%;
    width:100%;
    position: absolute;
    top:0;
    display: flex;
    flex-direction: column;
    width:100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: lightgrey;
}
.webcam-navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0 1rem 0;
}
div[class="webcam-overlay"] div {
    display:flex;
}
div[class="webcam-overlay"] li {
    background-color: white;
}
.webcam-nav-box {
    outline: none;
}
.webcam-footer {
    display: flex;
    justify-content: center;
    padding: 1rem 0 6rem 0;
}
.captured-footer, .captured-header, .send_to-footer {
    display: flex;
    justify-content: space-between;
    padding: 1rem 0 1rem 0;
}
.send_to-header {
    width: 100%;
    padding: 2rem 0 2rem 0;
    background-color: white;
}
.send_to-header ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.send-list {
    padding-top: 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.stories-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    justify-content: center;
}
.list-container {
    width: 96% !important;
    border-radius: 1rem;
    border: none;
    background-color: white;
}
ul[class*="add-list"] li {
    display: flex;
    justify-content: flex-start;
    padding: 1rem 1rem 1rem 1rem;
}
.footer-left, .header-left {
    display: flex;
    flex-direction: row;
    margin-left: 1rem;
}
.footer-right {
    margin-right: 1rem;
}
.header-right ul {
    display: flex;
    flex-direction: column !important;
}
li {
    margin: 0 0.5rem 0 0.5rem;
}
.buttons-overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top:0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.buttons-overlay button {
    margin: 1rem 0 1rem 0;
}
.image-preview {
    display: flex;
    justify-content: center;
    width: 100%;
}
